// components/StoreMap.tsx
"use client";

import React from "react";
import { YMaps, Map, Placemark } from "@pbe/react-yandex-maps";
import PlaceMarkImg from "@/assets/images/Placemark.png"
import YandexPlaceMark from "@/assets/icons/YandexPlaceMark";
export default function YandexMap() {
  const center: [number, number] = [41.271782, 69.197833];

  return (
    <div className="grid grid-cols-3 gap-[22px]">
      <div className="col-span-1">
        <h2 className="text-xl font-semibold">Основной склад</h2>
        <p>
          г. Ташкент, Мирзо-Улугбекский р-н, ул. Сайрам 7-тор (бывш.
          Э.Мараимова), д.52.
        </p>
        <p>
          Телефон:{" "}
          <a href="tel:+998555080660" className="text-cerulean underline">
            +998 55 508 06 60
          </a>
          , доб. 924
        </p>
        <p>
          Email:{" "}
          <a href="mailto:sales@nag.uz" className="text-cerulean underline">
            sales@nag.uz
          </a>
        </p>
        <p>Будни: 9:00 – 18:00</p>
        <p className="text-greenLight font-medium">Открыто ещё 6 часов</p>
        <a
          href={`https://yandex.ru/maps/?rtext=~${center[0]},${center[1]}&rtt=auto`}
          target="_blank"
          rel="noopener noreferrer"
          className="mt-2 inline-block text-cerulean underline"
        >
          Как добраться
        </a>
      </div>
      <div className="col-span-2 ">
        <YMaps>
          <Map
            defaultState={{ center, zoom: 16 }}
            width="100%"
            height="400px"
            modules={["control.ZoomControl", "control.FullscreenControl"]}
          >
            <Placemark
              geometry={center}
              properties={{ hintContent: "Основной склад" }}
              options={{
                iconLayout: "default#image",
                iconImageHref: <YandexPlaceMark/>,
                iconImageSize: [40, 40],
                iconImageOffset: [-20, -40],
              }}
            />
          </Map>
        </YMaps>
      </div>
    </div>
  );
}
